@Component({
    components: {Burger},
    mixins: [mixinNavLinks],
    data: () =>({
        searchPng,
        logoPng
    })
})
export default class Header extends Vue {
    handleShowMenu(): void {
        bus.$emit('nav-bar-show')
    }
